@import url(https://fonts.googleapis.com/css?family=Open+Sans:400,600,700,800&display=swap);
.item {
	padding: 10px;
	margin: 5px 15px 5px 10px;
	cursor: pointer;
	background: #FFFFFF;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

.item-selected {
	background: #FCC438;
}

.item.item-button {
	background: #C4C4C4;
	text-transform: uppercase;
	font-weight: 900;
}

.item.item-separator {
	background: none;
	text-transform: uppercase;
	font-weight: 900;
	cursor: inherit;
}

.search-input {
	background: #EDEDED;
	width: 100%;
	height: 40px;
	display: flex;
	padding: 0 5px 5px 10px;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
}

.search-input input[type="text"] {
	width: 100%;
	height: 30px;
	background: none;
	outline: none;
	border: none;
	font-size: 12px;
	letter-spacing: 0.03em;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	border-bottom: solid 1px black;
}

.search-icon {
	align-self: center;
	width: 20px;
	height: 24px;
	cursor: pointer;
}

.select {
	position: relative;
	padding: 0 10px;
	cursor: pointer;
	border: solid 1px black;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	background: #EDEDED;
}

.select-selected {
	border-color: #FCC438;
}

.select-label {
	height: 30px;
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.select-icon {
	align-self: center;
	width: 24px;
	height: 24px;
}

.select-dialog {
	display: flex;
	flex-direction: column;
	visibility: hidden;
	position: absolute;
	width: 100%;
	height: 400px;
	top: 100%;
	left: 0;
	z-index: 999;
	border: solid 1px black;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	background: #EDEDED;
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14),
							0 1px 5px 0 rgba(0, 0, 0, 0.12),
							0 3px 1px -2px rgba(0, 0, 0, 0.2);
}

.select-search {
	border-bottom: solid 1px black;
	margin-bottom: 10px;
}

.select-options {
	overflow: scroll;
}

.select-open .select-dialog {
	visibility: visible;
}

* {
	font-family: 'Open Sans';
	font-size: 12px;
	font-weight: normal;
	letter-spacing: 0.07em;
}

* * {
	font-size: inherit;
	letter-spacing: inherit;
	font-weight: inherit;
	text-transform: inherit;
}


h1 {
  font-size: 32px;
  letter-spacing: 0.03em;
  font-weight: 900;
  text-transform: uppercase;
	margin: 0;
}

h2 {
  font-size: 16px;
  font-weight: 800;
  letter-spacing: 0.03em;
  text-transform: uppercase;
	margin: 0;
}

a:link,
a:visited,
a:hover,
a:active {
	text-decoration: none;
	color: inherit;
	font-weight: 900;
}

strong,
.bold-weight {
  font-weight: 900;
}
.semi-bold-weight {
	font-weight: 600;
}
.uppercase {
  text-transform: uppercase;
}

.narrow-spacing {
  letter-spacing: 0.03em;
}
.regular-spacing {
  letter-spacing: 0.07em;
}
.comfortable-spacing {
  letter-spacing: 0.1em;
}
.wide-spacing {
  letter-spacing: 0.13em;
}

.right-align {
  text-align: right;
}
.center-align {
  text-align: center;
}

.large-size {
  font-size: 18px;
}
.medium-size {
  font-size: 14px;
}
.small-size {
  font-size: 10px;
}

.thumbnail {
	cursor: pointer;
}

.thumbnail .image {
	width: 100%;
	height: 198px;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	border: solid 1px black;
}

.backdrop {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	-webkit-transition-property: opacity;
	transition-property: opacity;
	background: rgba(0, 0, 0, 0.5);
}

.backdrop-light {
	position: absolute;
	top: 0;
	right: 0;
	bottom: 0;
	left: 0;
	-webkit-transition-property: opacity;
	transition-property: opacity;
	background: rgba(255, 255, 255, 0.5);
}

.card {
	display: flex;
	flex-direction: column;
	position: relative;
	background: #FCC438;
	width: 100%;
	padding: 24px;
	box-sizing: border-box;
	-moz-box-sizing: border-box;
	-webkit-box-sizing: border-box;
	max-height: 720px;
	max-width: 100%;
	width: 100%;
}

.card-subtitle {
	margin: 12px 0 2px 0;
}

.card-link {
	position: absolute;
	top: 25px;
	right: 25px;
	width: 25px;
	height: 25px;
}

